import { useHashParams } from "@rp/react/web/common/hooks"

type UseDocumentsCollectionParametersProps = {
  documentIdFromProps: string | undefined
  collectionIdFromProps: string | undefined
}

type UseDocumentsCollectionParametersState = {
  documentId: string
  collectionId: string
}

const useDocumentsCollectionParameters = ({
  documentIdFromProps,
  collectionIdFromProps,
}: UseDocumentsCollectionParametersProps): UseDocumentsCollectionParametersState => {
  const [{ document: documentIdFromURL, collection: collectionIdFromURL }] = useHashParams()

  const documentId = documentIdFromProps || documentIdFromURL || ""
  const collectionId = collectionIdFromProps || collectionIdFromURL || ""

  return { documentId, collectionId }
}

export { useDocumentsCollectionParameters }
export type { UseDocumentsCollectionParametersState, UseDocumentsCollectionParametersProps }
